export default {
    init: function() {
        let rangeStart = document.getElementById("range_pub_date_si_begin")
        let rangeEnd = document.getElementById("range_pub_date_si_end")
        let textExplainer = document.querySelectorAll("p.range.subsection.slider_js")
        if(!rangeStart || !rangeEnd || !textExplainer) return
        rangeStart.placeholder = "Min year"
        rangeEnd.placeholder = "Max year"
        textExplainer[0].html = "Custom Year Range"
    }
}